'use client';

import React, { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { useAuth } from '@/contexts/SupabaseAuthContext';
import { useUserData } from '@/contexts/UserDataContext';
import { supabaseService } from '@/services/supabaseService';
import { Plus, BookOpen, Hash, Users } from 'lucide-react';

interface CreatePostDialogProps {
  children: React.ReactNode;
  onPostCreated?: () => void;
}

export function CreatePostDialog({ children, onPostCreated }: CreatePostDialogProps) {
  const { user: authUser } = useAuth();
  const { user } = useUserData();
  const [open, setOpen] = useState(false);
  const [content, setContent] = useState('');
  const [verseReference, setVerseReference] = useState('');
  const [verseText, setVerseText] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [tagInput, setTagInput] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleAddTag = () => {
    if (tagInput.trim() && !tags.includes(tagInput.trim())) {
      setTags([...tags, tagInput.trim()]);
      setTagInput('');
    }
  };

  const handleRemoveTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!authUser || !content.trim()) return;

    setIsSubmitting(true);
    try {
      // Create post in database
      const { data, error } = await supabaseService.getClient()
        .from('community_posts')
        .insert({
          user_id: authUser.id,
          content: content.trim(),
          verse_reference: verseReference.trim() || null,
          verse_text: verseText.trim() || null,
          tags: tags.length > 0 ? tags : null,
          likes_count: 0,
          comments_count: 0,
          shares_count: 0,
        })
        .select()
        .single();

      if (error) {
        console.error('Error creating post:', error);
        throw error;
      }

      // Update user stats
      const currentStats = await supabaseService.getUserStats(authUser.id);
      await supabaseService.updateUserStats(authUser.id, {
        community_posts: (currentStats?.community_posts || 0) + 1,
      });

      // Award points for posting
      await supabaseService.addPoints(authUser.id, 5);

      // Reset form
      setContent('');
      setVerseReference('');
      setVerseText('');
      setTags([]);
      setTagInput('');
      setOpen(false);

      // Notify parent component
      onPostCreated?.();

      console.log('Post created successfully:', data);
    } catch (error) {
      console.error('Error creating post:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        {children}
      </DialogTrigger>
      <DialogContent className="sm:max-w-[500px]">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Plus className="h-5 w-5" />
            Create New Post
          </DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Content */}
          <div className="space-y-2">
            <Label htmlFor="content">Share your thoughts...</Label>
            <Textarea
              id="content"
              placeholder="What's on your mind? Share your spiritual journey, insights, or prayer requests..."
              value={content}
              onChange={(e) => setContent(e.target.value)}
              className="min-h-[100px]"
              required
            />
          </div>

          {/* Verse Reference */}
          <div className="space-y-2">
            <Label htmlFor="verse-reference">
              <BookOpen className="inline h-4 w-4 mr-1" />
              Verse Reference (Optional)
            </Label>
            <Input
              id="verse-reference"
              placeholder="e.g., John 3:16"
              value={verseReference}
              onChange={(e) => setVerseReference(e.target.value)}
            />
          </div>

          {/* Verse Text */}
          {verseReference && (
            <div className="space-y-2">
              <Label htmlFor="verse-text">Verse Text (Optional)</Label>
              <Textarea
                id="verse-text"
                placeholder="Enter the verse text..."
                value={verseText}
                onChange={(e) => setVerseText(e.target.value)}
                className="min-h-[60px]"
              />
            </div>
          )}

          {/* Tags */}
          <div className="space-y-2">
            <Label htmlFor="tags">
              <Hash className="inline h-4 w-4 mr-1" />
              Tags (Optional)
            </Label>
            <div className="flex gap-2">
              <Input
                id="tags"
                placeholder="Add a tag..."
                value={tagInput}
                onChange={(e) => setTagInput(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && (e.preventDefault(), handleAddTag())}
              />
              <Button type="button" variant="outline" onClick={handleAddTag}>
                Add
              </Button>
            </div>
            {tags.length > 0 && (
              <div className="flex flex-wrap gap-1 mt-2">
                {tags.map((tag) => (
                  <Badge key={tag} variant="secondary" className="cursor-pointer">
                    #{tag}
                    <button
                      type="button"
                      onClick={() => handleRemoveTag(tag)}
                      className="ml-1 text-xs hover:text-destructive"
                    >
                      ×
                    </button>
                  </Badge>
                ))}
              </div>
            )}
          </div>

          {/* Submit Buttons */}
          <div className="flex justify-end gap-2 pt-4">
            <Button type="button" variant="outline" onClick={() => setOpen(false)}>
              Cancel
            </Button>
            <Button type="submit" disabled={!content.trim() || isSubmitting}>
              {isSubmitting ? 'Posting...' : 'Share Post'}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
